export default {
	data() {
		return {
			globaluser:{}
		}
	},
	onLoad() {
		var me = this;
		var globaluser = uni.getStorageSync("globalUser");
		me.globaluser = globaluser;
	},
	methods: {
		formSubmitNickname(e){
			var me = this;
			var nickname = e.detail.value.nickname;
			me.globaluser.name = nickname;
			
			uni.setStorageSync("globalUser",me.globaluser);
			uni.switchTab({
				url:"../me/me"
			})
		}
	}
}
